export var rgb = /rgb\((\d+),(\d+),(\d+)\)/; // Parse reference id

export var reference = /(#[a-z0-9\-_]+)/i; // splits a transformation chain

export var transforms = /\)\s*,?\s*/; // Whitespace

export var whitespace = /\s/g; // Test hex value

export var isHex = /^#[a-f0-9]{3,6}$/i; // Test rgb value

export var isRgb = /^rgb\(/; // Test css declaration

